.action-bar {
  height: 53px;
}

.action-bar--bottom {
  position: absolute;
  bottom: 0;
  background-color: #f4f6f8;
  border-top: 1px solid #dbe0e9;
  left: 0;
  padding-left: 1.5385rem;
  padding-right: 1.5385rem;
}

.action-bar .btn-toolbar {
  margin-top: auto;
  margin-bottom: auto;
}

.left-and-main {
  position: relative;
}

.left-and-main .toolbar__navigation {
  height: 100%;
}

.nav-tabs {
  margin-bottom: 0;
  padding-left: 0;
  height: 100%;
  display: flex;
}

.nav-tabs__tab-item {
  list-style-type: none;
}

.nav-tabs__tab-item a {
  display: inline-block;
  outline: none;
  float: none;
  color: #566b8d;
  padding: 1.2308rem 0.3077rem 1.0769rem;
  line-height: 20px;
}

.nav-tabs__tab-item a:hover {
  border-bottom: 0.15385rem solid #aebace;
  text-decoration: none;
}

.nav-tabs__tab-item:not(:first-of-type) {
  margin-left: 0.6154rem;
}

.nav-tabs__tab-item--current a,
.nav-tabs__tab-item--current a:hover {
  border-bottom: 0.15385rem solid #43536d;
  cursor: text;
}

.error-message {
  text-align: center;
}

.react-admin {
  width: 100%;
}




